html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #dda8b5;
  background: #4d2a4f;
}


.container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100vw;
  height: 100vh;
  
}
form {
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 400px;
  margin: auto;
 
}
input,
button {
  box-sizing: border-box;
  height: 3em;
  margin: 1em;
}
button, input[type=submit]{
  padding: 1em;
      border: 2px solid #361e40 ;
      border-radius: 10px;
      color: #dda8b5;
      background: #29102e;
}

button:hover, input[type=submit]:hover{
  color: #29102e;
  background: #dda8b5;
  cursor: pointer;
}
.local-login {
 flex: 2;
  border-left: thick solid #dda8b5;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  
}
.social-login {
  flex: 1;
  
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.btn-wraper{
  width: 100%;
}
.social-login button{
  display: block;
  width: 90%;
}
span {
  color: #dda8b5;
  text-decoration: underline;
  cursor: pointer;
}
span:hover {
  color: #29102e;
}